@font-face {
  font-family: "Muller";
  font-style: normal;
  font-weight: 400;
  src: local("Muller light"), url("../fonts/mullerlight-webfont.woff2") format("woff2");
}

@font-face {
  font-family: "PlayfairDisplay";
  font-style: normal;
  font-weight: 700;
  src: local("PlayfairDisplay bold"), url("../fonts/playfair_display_bold-webfont.woff2") format("woff2");
}

@font-face {
  font-family: "Raleway";
  font-style: normal;
  font-weight: 400;
  src: local("Raleway light"), url("../fonts/raleway-light-webfont.woff2") format("woff2");
}

@font-face {
  font-family: "Raleway";
  font-style: normal;
  font-weight: 500;
  src: local("Raleway semibold"), url("../fonts/raleway-semibold-webfont.woff2") format("woff2");
}

@font-face {
  font-family: "Raleway";
  font-style: normal;
  font-weight: 700;
  src: local("Raleway extrabold"), url("../fonts/raleway-extrabold-webfont.woff2") format("woff2");
}

body,
html {
  margin: 0;
}

ul,
li {
  list-style: none;
  padding: 0;
  margin: 0;
}

p {
  margin: 0;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}

body {
  font-family: "Raleway", "Muller", "PlayfairDisplay", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 25px;
  letter-spacing: 1px;
  color: #5e5e5e;
  background-color: #bbb;
  /* #fdfaf3 */
}

html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

.visually-hidden {
  position: absolute;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px;
}

.wrapper {
  max-width: 1170px;
  margin: 0 37px;
  position: relative;
}

.wrapper2 {
  max-width: 1170px;
  margin: 0 auto;
}

.centering {
  display: flex;
  justify-content: center;
}

.slider-button,
.button-arrow,
.play,
.menu,
.arrow {
  font-size: 0;
  cursor: pointer;
}

a {
  cursor: pointer;
  text-decoration: none;
  color: #7d7d7d;
  font-size: 1.125em;
  line-height: 30px;
}

.submit-orange {
  cursor: pointer;
}

.phone-number {
  font-family: "Muller";
  color: #252525;
  font-size: 0.875em;
  letter-spacing: 1.5px;
}

.intro-text {
  color: #ffffff;
  font-size: 0.875em;
  line-height: 20px;
}

h1 {
  font-family: "PlayfairDisplay";
  font-size: 3.938em;
  line-height: 58px;
  color: #ffffff;
  letter-spacing: 1.5px;
}

.button-hover,
.submit-orange {
  display: block;
  font-weight: 700;
  font-size: 0.938em;
  line-height: 16px;
  color: #ffffff;
  text-transform: uppercase;
}

.button-hover {
  border: 2px solid #fff;
}

.button-hover:hover {
  background-color: #f8a25a;
  text-decoration: none;
  color: #fff;
}

.button-hover:active {
  background-color: #ef862f;
}

.headline {
  font-family: "PlayfairDisplay";
  color: #00203a;
  font-size: 3em;
}

.navigation-ref {
  text-transform: uppercase;
  font-size: 1.25em;
  line-height: 42px;
}

.feature-item .feature-headline {
  font-weight: 500;
  font-size: 1.375em;
  color: #1a292f;
}

.projects-photo {
  cursor: pointer;
}

.services-ref.current {
  font-weight: 600;
}

.current-ref,
.services-ref.current,
.service-column span {
  color: #00203a;
}

.service-headline {
  font-family: "PlayfairDisplay";
  font-weight: 400;
  font-size: 2.1em;
  color: #00203a;
}

.service-column span,
.service-text {
  font-size: 1.125em;
  line-height: 30px;
  letter-spacing: 0;
}

.video-text {
  font-family: "Raleway";
  font-size: 1.19em;
  color: #ffffff;
}

.steps-headline {
  font-weight: 500;
  font-size: 1.375em;
  line-height: 17px;
  color: #252525;
}

.steps-number {
  font-size: 0.75em;
}

.form-headline {
  font-family: "PlayfairDisplay";
  font-size: 2.813em;
  color: #ffffff;
  line-height: 45px;
}

.form-text,
.user-text,
.form-text2 {
  font-size: 0.875em;
  line-height: 22px;
  color: #918e8b;
}

.client-name {
  font-weight: 500;
  font-size: 1.375em;
  line-height: 30px;
  color: #252525;
}

.feedbacks-name {
  font-weight: 700;
  color: #efb172;
}

.sgn1,
.sgn2 {
  color: #ffc487;
}

.main-header {
