  height: 3px;
  padding: 0;
  background: #cdd3cf;
}

.slider-box1 li button::before {
  content: '';
  width: 26px;
  height: 3px;
}

.slider-box1 li.slick-active button {
  background: #1a444a;
}

.intro-column {
  position: absolute;
  max-width: 325px;
  top: 170px;
  left: 94px;
}

.intro-column .intro-text {
  margin-top: 26px;
  word-wrap: break-word;
}

.intro-column .button-hover {
  margin-top: 35px;
  padding: 20px 78px 17px 28px;
  max-width: 278px;
  position: relative;
}

.intro-column .button-hover::after {
  content: "";
  position: absolute;
  background-image: url(../img/Arrow-OutRight-icon.png);
  background-repeat: no-repeat;
  background-position-x: 18px;
  background-position-y: 2px;
  height: 22px;
  width: 100px;
}

.menu-wrap {
  position: absolute;
  left: 2px;
  top: -54px;
}

.menu-wrap .show-nav {
  display: block;
  background-image: url("../img/bg.png");
  position: absolute;
  top: 54px;
  left: 0;
  width: 1161px;
  height: 595px;
}

.navigation-wrap {
  display: none;
}

.navigation-list {
  margin: auto;
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  align-items: center;
  height: 100%;
  padding-top: 110px;
}

.navigation-item {
  margin-top: 12px;
}

.navigation-item .navigation-ref:hover {
  color: #f8a25a;
}

.navigation-item .navigation-ref:active {
