}

.phone-wrap {
  position: absolute;
  top: -58px;
  right: 4px;
}

.phone-wrap:hover span {
  font-weight: 700;
}

.phone-wrap:hover svg {
  animation: phone-animation 1s infinite ease-in;
}

@keyframes phone-animation {
  0% {
    transform: scale(2);
  }

  50% {
    transform: rotate(360deg);
  }

  100% {
    transform: scale(2);
  }
}

.features {
  margin-bottom: 138px;
}

.features-list {
  display: flex;
  flex-wrap: wrap;
}

.feature-item {
  width: 25%;
  padding-right: 15px;
}

.feature-item:nth-child(2),
.feature-item:nth-child(4) {
