}

.feature-item-wrap .number {
  display: block;
  color: #fde3c9;
  font-family: "PlayfairDisplay";
  font-size: 85px;
  margin-bottom: 40px;
}

.feature-item-wrap .feature-headline {
  display: block;
  max-width: 280px;
  margin-bottom: 12px;
  letter-spacing: 0;
}

.portfolio {
  margin-bottom: 136px;
}

.portfolio-nav {
