  font-weight: 600;
}

.portfolio-item:active {
  border-bottom: 1px solid #000;
  padding-bottom: 5px;
}

.portfolio-item .ref {
  letter-spacing: 0;
}

.portfolio-item .current-ref {
  font-weight: 600;
  border-bottom: 1px solid #000;
  padding-bottom: 10px;
  letter-spacing: 0;
}

.portfolio-item:nth-child(2) {
