}

.projects-slider {
  display: flex;
}

.projects-photo {
  position: relative;
  width: 25%;
  height: 552px;
}

.projects-photo:hover {
  width: 40%;
}

.projects-photo:hover .overlay,
.projects-photo:hover .design,
.projects-photo:hover .rotate {
  display: none;
}

.projects-photo:hover .arrow {
  filter: invert(100%);
  transform: scale(1);
}

.projects-photo:hover a {
  display: block;
  position: absolute;
  top: 54%;
  left: 52%;
  transform: translate(-50%, -50%);
}

.projects-photo:active svg {
  opacity: 0.5;
}

.projects-photo:active img {
  opacity: 0.8;
}

.projects-photo img {
  width: 100%;
}

.projects-photo .overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  background-color: #112537;
  height: 552px;
  opacity: 0.8;
  z-index: 5;
}

.projects-photo a {
  display: none;
}

.design {
  position: absolute;
  top: 0;
  left: 20;
  color: #ffffff;
  font-weight: 500;
  line-height: 22px;
  padding-left: 28px;
  padding-top: 26px;
  max-width: 180px;
  z-index: 10;
}

.rotate {
  position: absolute;
  bottom: 31px;
  left: 18px;
