}

.video-box {
  position: relative;
  margin-bottom: 104px;
}

.video-box .button-wrap {
  position: absolute;
  display: flex;
  flex-direction: column;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.video-wrap {
  position: relative;
  padding-top: 45%;
}

.video {
  width: 100%;
  height: auto;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  top: 0;
}

.play {
  position: relative;
  border: none;
  background-color: transparent;
  background-image: url("../img/play.png");
  background-repeat: no-repeat;
  background-position: 50% 50%;
  width: 18px;
  height: 21px;
  align-self: center;
  margin-bottom: 53px;
}

.play:hover::before {
  background-color: #f8a25a;
}

.play:active::before {
  opacity: 0.5;
}

.play::before {
  position: absolute;
  content: "";
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 84px;
  height: 84px;
  border-radius: 50%;
  background-color: #ffc487;
  z-index: -1;
}

.steps {
  margin-bottom: 80px;
}

.steps-number {
  position: relative;
}

.steps-number::before {
  position: absolute;
  content: "";
  height: 37px;
  width: 37px;
  top: -10px;
  left: -15px;
  border-radius: 50%;
  background-color: transparent;
  border: 1px solid #fde5d1;
}

.steps-number::after {
  position: absolute;
  content: "";
  height: 53px;
  width: 53px;
  top: -17px;
  left: -23px;
  border-radius: 50%;
  background-color: transparent;
  border: 1px solid #fde5d1;
}

.number-wrap {
  position: relative;
}

.number-wrap::before {
  position: absolute;
  content: "";
  height: 21px;
  width: 21px;
  border-radius: 50%;
  top: 4px;
  left: 33px;
  background-color: #fde5d1;
  z-index: -1;
}

.number-wrap::after {
  position: absolute;
  content: "";
  height: 72px;
  width: 72px;
  top: -18px;
  left: 7px;
  border-radius: 50%;
  background-color: transparent;
  border: 1px solid #fde5d1;
}

.steps-list {
