.services {
    margin-bottom: 134px;
}

.services-wrap {
    display: flex;
    padding-top: 79px;
}

.services-nav {
    display: flex;
    flex-shrink: 0;
    flex-direction: column;
    padding-right: 54px;
    justify-content: space-between;
    min-width: 278px;
    margin-top: 10px;
}

.services-item {
    max-width: 168px;
    line-height: 21px;
    flex-shrink: 0;
    &.current {
        min-width: 300px;
        padding-left: 24px;
        line-height: 19px;
        letter-spacing: 0;
    }
    &:hover {
        a {
            color: @headline-color;
        } 
    }        
    .services-ref {
        line-height: 21px;
        display: inline-block;
        letter-spacing: 0;
    }    
}

.service-column {
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    max-height: 712px;
    span {
        font-size: 1.200em;
    }
    .button-white {
        max-width: 216px;
        padding: 17px 38px;
        color: @headline-color; 
        border: 2px solid #00203a;
        max-height: 58px;
        margin-top: 56px;
        align-self: end;
        &:hover {
            color: @color-white;
            border-color: @color-white;         
        }
    }
}

.services-photo {
    width: 100%;
    margin-bottom: 60px;
}

.service-col1, 
.service-col2  {
    display: flex;
    justify-content: space-between;
}

.service-col1 {   
    margin-bottom: 14px;
}

.service-text {
    max-width: 498px;
    word-break: break-word;
}
    